
.row {
  margin-top: 10px;
}


.Square {
  height:100px;
  width:100px;
  font-family: var(--font-mono);
  font-size: 3rem;
  font-weight: 900;
  border: 3px solid var(--color-bg);
  background: var(--color-text);
  color: var(--color-bg);
  opacity: 0.75;
  background-color:gray;

  /* transition for the hover and active states */
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  background: radial-gradient(ellipse at center, rgb(138, 138, 138) 0%,rgb(57, 57, 57) 100%);
}
/* on hover slightly increase the size of the square */
.Square:hover {
  opacity: 1;
}
/* when active decrease the size of the square */
.Square:active {
  opacity: 1;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}


.Board {
  /* for the board create a grid of 9 squares, in three columns and rows */
  display: grid;
  grid-template-columns: repeat(3, 33%);
  grid-template-rows: repeat(3, 33%);
  grid-gap: 2px;
  margin: 1rem 0 2rem;
  width: 300px;
}


html,body {
  height:100%;
  width:100%;
  padding:0px;
  margin:0px;
}
body {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(181,189,200,1)),color-stop(36%, rgb(83, 122, 158)),to(rgba(40,52,59,1)));
  background: linear-gradient(to bottom, rgba(181,189,200,1) 0%,rgb(83, 122, 158) 36%,rgba(40,52,59,1) 100%);
}
